export default {
	props: {
		time: {
			type: Number,
			default: 2
		},
		value: {
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			values: 0,
			count:0,
		}
	},
	watch: {
		value(val) {
			if (val > 0) {
				this.values = val;
				this.numberGrow()
			}
		}
	},
	methods: {
		numberGrow() {
			let step = parseInt((this.values * 10) / (this.time * 1000))
			let current = 0
			let start = 0
			let t = setInterval(() => {
				start += step
				if (start > this.values) {
					clearInterval(t)
					start = this.values
					t = null
				}
				if (start == 0) {
					start = this.values;
					clearInterval(t)
				}
				if (current === start) {
					return
				}
				current = start
				this.count = current.toString().replace(/(\d)(?=(?:\d{3}[+]?)+$)/g, '$1,')
			}, 10)
		}
	},
}
